.person {
  width: fit-content;
  margin-bottom: 16px;
  padding: 8px;
  border: 1px solid #000;
  border-radius: 8px;
  font-family: Arial, Helvetica, sans-serif;

  &__name {
    margin-top: 8px;
  }

  &__age {
    color: #00f;
    font-weight: bold;
  }

  &__partner {
    color: green;
    font-size: 15px;
  }
}
